class Item{
  constructor(name, description, value, icon, quantity=1){
    this.name = name;
    this.icon = icon;
    this.description = description;
    this.value = value;
    this.quantity = quantity;
  }

  get card(){
    return `<img class="inventory-item" data-item-type="${this.name}" src="${this.icon}"></img>`;
  }
}

