class Upgrade{
  constructor(name, description, cost, prereqs, onActivate, purchased=false){
    this.name = name;
    this.description = description[0] + '<br/><i>' + description[1] + '</i>';
    this.cost = cost;
    this.prereqs = prereqs;
    this.onActivate = onActivate;
    this.purchased = purchased;
  }

  purchase(){
    if (cost <= getGameVars('coins') && !this.purchased){
      outerUpdate('coins', cost * -1);
      this.purchased = true;
      this.onActivate();
    }
  }

  get card(){
    var cardHTML = upgradeCard(this);
    return cardHTML;
  }
}

function makeItemsDraggable(){
  $('.inventory-item').draggable({
    start: function(event, ui){
      $(this).css('z-index', 100);
    },
    revert: 'invalid',
    stop: function(event, ui){
      $(this).css('z-index', 10);
    }
  });
}

