function makeSlotsDroppable(){
  // configure inventory slots
  $('.inventory-slot').droppable({
    accept: function(draggable){
      // checks parents until finding one w/ match
      // then takes allowed data element
      var allowedRaw = $(this).data('allowed');
      if (typeof allowedRaw === 'string'){
        allowedRaw = [allowedRaw];
      } else {
        console.error('Inventory slot has no allowed values');
        console.log($(this));
      }

      // deny null comers
      if (allowedRaw == [""] || allowedRaw == [null] || allowedRaw == []){
        return false;
      }

      if (allowedRaw == 'all' || 0 <= allowedRaw.indexOf('all')){
        // if it accepts all comers, let it through
        return true;
      } else if (-1 < allowedRaw.indexOf(draggable.data('item-type'))){
        // if the class is on the allowed list, let it through
        return true;
      } else {
        return false;
      }
    },

    drop: function(event, ui) {
      var droppable = $(this);
      var dropCords = $(this).offset();
      dropCords.top += $(this).height() / 2;
      dropCords.left += $(this).width() / 2;
      var dragCords = ui.draggable.offset();
      dragCords.top += ui.draggable.height()/2;
      dragCords.left += ui.draggable.width()/2;
      var deltaTop = dropCords.top - dragCords.top;
      var deltaLeft = dropCords.left - dragCords.left;
      ui.draggable.animate({
        top: '+=' + deltaTop,
        left: '+=' + deltaLeft
      }, function(){
        ui.draggable.css({top:1, left:1});
        transferItem(ui.draggable, droppable);
        ui.draggable.appendTo(droppable);
      });
    }
  });
}

function getBestConsultant(consultants, skill){
  var bestConsultant = {skills: {}};
  bestConsultant.skills[skill] = 0;

  for (let consultant in getGameVars('consultants')){
    if (skill in consultant.skills){
      if (bestConsultant.skills[skill] < consultant.skills[skill]){
        bestConsultant = consultant;
      }
    }
  }
  if ('name' in bestConsultant){
    return bestConsultant;
  } else {
    return false;
  }
}

function businessInvestmentPopup(money, business){
  // pre-checks
  Object.setPrototypeOf(business.investmentInventory, Inventory.prototype);
  if (money < 0 || (money == 0 && business.investmentInventory.empty)){
    console.error("Illegal Investment");
    return -1
  }

  var popUp = investmentModal({});
  var skillsText = "";
  var outcomeText = "";
  var cashValue = 0;
  var materialValue = 0;

  var consultants = getGameVars('consultants')
  var barterer = getBestConsultant(consultants, 'barter');
  var constructor = getBestConsultant(consultants, 'construction');

  if (barterer || constructor){
    skillsText += "Relevant Skills:";

    if (barterer){
      skillsText += `
        ${barterer.name}:
          Barter ${barterer.skills.barter}`
    }
    if (constructor){
      if (constructor != barterer){
        skillsText += `
          ${constructor.name}:`
      }
      skillsText += `
          Construction ${constructor.skills.construction}`;
    }

    skillsText += `
    <hr/>;`
  }

  if (0 < money){
    var doshRoll = rollDice('1d20');
    outcomeText += `${money} coins (Barter ${doshRoll}`
    if (barterer){
      outComeText += `(+${barterer.skills.barter})`;
      doshRoll += barterer.skills.barter;
    }
    cashValue = Math.floor((money/2) * (1+(doshRoll/20)));
    outcomeText += `): +${cashValue} value`;
  }

  if (!business.investmentInventory.empty){
    var constructionRoll = rollDice('1d20');
    var rawMaterial= business.investmentInventory.items[0];
    outcomeText += `
    <br>1 ${rawMaterial.name} (${rawMaterial.value}) (Construction ${constructionRoll}`;
    if (constructor){
      outcomeText += `(+${constructor.skills.construction + 5})`;
      constructionRoll += constructor.skills.construction;
    } else {
      outcomeText += `(+5)`;
    }
    materialValue = Math.ceil((rawMaterial.value/2) * (1+((5+constructionRoll)/20)))
    outcomeText += `): +${materialValue} value`;
  }

  popUp = popUp.replace('XXXINVESTMENTRESULTSKILLSXXX', skillsText);
  popUp = popUp.replace('XXXINVESTMENTRESULTSUMMARY', outcomeText);

  $(popUp).appendTo('body');
  $('#investment-modal').modal('show');
  $("#investment-modal").on('hidden.bs.modal', function () {
    $("#investment-modal").remove();
  });

  // remove fees
  outerUpdate('coins', -1 * money);
  business.investmentInventory.clear();
  business.value += cashValue + materialValue;

  // visual remove fees
  business.updateCard();

}


class Inventory{
  constructor(name, q, allowed=['all']){
    this.name = name;
    this.items = new Array(q).fill(null);
    this.id = name.replace(/\W+/g, '-').toLowerCase();
    if (allowed[0] == 'all'){
      this.allowed = allowed;
    } else {
      this.allowed = allowed.map(function(a){return a.name});
    }
    this.active = true;

    console.log(`created ${this.name} inventory`);
  }

  get summary(){
    var textSummary = [];
    if (!this.empty){
      var itemNames = this.items.map(function(a){return a.name}).sort();
      for (var itemName of itemNames){
        // creates temp list of all objects w/ matching name, then uses length as added quantity
        var allOne = this.items.filter((obj) => obj.name === itemName);

        // "wood: +1(50)"
        // "wood: +3(50/ea)"
        textSummary.push(`${itemName}: +${allOne.length} (${allOne[0].value}${allOne.length == 1 ? '': '/ea'})`);
      }
      return textSummary.join('/n')
    }
  }

  get card(){
    var cardHtml = `<div id="${this.id}" class="card">`;
    cardHtml += `<div class="row m-1">`;
    for (var i=0; i<this.items.length; i++){
      // data-containing-inventory used to check the inventory object for allowed items
      // data-inventory-slot-number used to add/remove items from inventory object
      cardHtml += `<div class="inventory-slot" data-containing-inventory="${this.id}" data-allowed="${this.allowed}" data-inventory-slot-number=${i}>`
      if (this.items[i] != null){
        if (!(this.items[i] instanceof Item)){
          Object.setPrototypeOf(this.items[i], Item.prototype);
        }
        cardHtml += this.items[i].card;
      }
      cardHtml+= '</div>';
    }
    // close out whole thing and row around inventory squares.
    cardHtml += '</div></div>';
    return cardHtml;
  }

  get empty(){
    return this.items.join().replace(/,/g,'').length === 0;
  }

  clear(){
    this.items = new Array(this.items.length).fill(null)
  }

  add(item){
    if (-1 < this.items.indexOf(null)){
      console.log(`adding ${item.name} to ${this.name}`);
      this.items[this.items.indexOf(null)] = item;
    }
  }
}




function floater(text, identifier){
  var targetCoords = $(identifier).position();
  $(identifier).after(`<p class='floater' style='position: absolute; z-index: 5; left: ${targetCoords.left}px; top: ${targetCoords.top}px;'>${text}</p>`);
  $(identifier).next().animate({
    opacity:0,
    left: "+=5",
    top: "-=30",
  }, 1000, function(){
    $(this).remove();
  });
}

// simplistic, does not actually pluralize complex nouns
function pluralize(x, q){
  if (q == 1){
    return x;
  } else {
    return x + "s";
  }
}

function outerUpdate(keyName, value){
  var delta = JSON.parse(localStorage.getItem('deltas'));
  if (keyName in delta){
    delta[keyName] += value;
  } else {
    delta[keyName] = value;
  }
  localStorage.setItem('deltas', JSON.stringify(delta));
}


// 3 weeks 4 days 23:11:05
function formatTime(seconds){
  if (seconds < 1){
    return "Done";
  }
  var returnStr = "";
  if (604800 <= seconds){
    returnStr += `${parseInt(seconds/604800)} ${pluralize("week", parseInt(seconds/604800))}, `;
    seconds %= 604800;
  }
  if (86400 <= seconds){
    returnStr += `${parseInt(seconds/86400)} ${pluralize("day", parseInt(seconds/86400))}, `;
    seconds %= 86400;
  }
  returnStr += ((seconds/3600 < 10) ? '0' : '') + parseInt(seconds/3600);
  seconds %= 3600;
  returnStr += ":" + ((seconds/60 < 10) ? '0' : '') + parseInt(seconds/60);
  seconds %= 60;
  returnStr += ":" + ((seconds < 10) ? '0' : '') + parseInt(seconds);
  return returnStr;
}

function rollDice(dice){
  // add a + if the first digit is not negative.  helps w/ regex.
	if (dice[0] != '-'){
  	dice = '+' + dice;
  }
  // catches all "+1d6" or "-2d8" formats
  var dieArray = [...dice.matchAll(/([+-][\dd]*)/g)];
  // remove double storage of matches
  dieArray = dieArray.map(x => x[0]);

  var sum = 0;
	for (var die of dieArray){
  	if (die.includes('d')){
      // (q, v)
    	var splitVal = die.slice(1,).split('d');
      // prevent string as int problems
      splitVal = splitVal.map(x => parseInt(x));
      for (var i = 0; i < splitVal[0]; i++){
      	if (die[0] == '+'){
          // random number 1 to splitVal[1]
          sum += Math.floor((Math.random() * splitVal[1]) + 1);
        } else if (die[0] == '-') {
          sum -= Math.floor((Math.random() * splitVal[1]) + 1);
        } else {
          // If not + or - as first digit of a die
        	console.error("Failed while rolling: " + die);
        }
       }

    } else {
    	// covers both + and -
    	sum += parseInt(die);
    }
  }
  return sum;
}

export {Item, Inventory, Upgrade, floater, formatTime, rollDice, outerUpdate, pluralize, humanizedMap, itemList, upgradeList, makeItemsDraggable, makeSlotsDroppable, businessInvestmentPopup};
