import 'bootstrap';
import './main.scss';
import log from './data/log.json';
import {Business} from './business.js';
import {makeItemsDraggable, formatTime, floater, pluralize, humanizedMap, Inventory, itemList, Item, upgradeList, makeSlotsDroppable} from './toolset.js';

// Default values, only used on new game
var timeDelta = 1;
var gameVars = {
  "savedTime": 604800, // 604800 seconds in a week
  "coins": 100,
  "startTime": new Date(),
  "lastSeen": new Date(),
  "logContents": ['intro', 'lumber-keep'],
  "unlocked": [],
  "ownedBusinesses": new Array(),
  "globalEvents": [],
  "inventories": {},
  "ownedConsultants": {'you':{
    name: "you",
    skills: {
      barter: 2
    }
  }},
};
var selected = null;

function getGameVars(variableName){
  return gameVars[variableName];
}

function updateLocalVariables() {
  gameVars["lastSeen"] = new Date();

  for (const gameVar in gameVars) {
    localStorage.setItem(gameVar, JSON.stringify(gameVars[gameVar]));
  }
};

function pullLocalVariables() {
  if(!localStorage.getItem("lastSeen")){
    console.log("first time vars set")
    updateLocalVariables();
    //TODO: addToStoryLog(firstTwoTexts)
  } else {
    console.log("return trip vars pulled");
    // set script values
    for (const gameVar in gameVars) {
      gameVars[gameVar] = JSON.parse(localStorage.getItem(gameVar));
    }
    if (gameVars['unlocked'] == 'null' || !gameVars['unlocked']){
      gameVars['unlocked'] = [];
    }
    // Add time since last online to savedTime
    let newTime = new Date();
    let missingTime = parseInt((newTime.getTime() - Date.parse(gameVars["lastSeen"]))/1000);
    console.log(missingTime + " seconds since last seen");
    localStorage.setItem("savedTime", missingTime + parseInt(localStorage.getItem("savedTime")));

    // Make businesses back into business classes
    gameVars['ownedBusinesses'].forEach((item, i) => {
      Object.setPrototypeOf(gameVars['ownedBusinesses'][i], Business.prototype);
    });
    for (let business of gameVars['ownedBusinesses']){
      business.catchUp(missingTime);
    }

    window.GLOBAL_EVENTS = gameVars['globalEvents'];
  }
};

function incrementTime(quantity) {
  gameVars["savedTime"] = parseInt(gameVars["savedTime"]) + quantity;
};

function updateStatsDisplay() {
  $('#currency-list').empty();
  // Each stat added here
  var statsText = `<b>Time:</b> ${formatTime(gameVars["savedTime"])}`;
  if (gameVars.coins){
    statsText += `<br><b>Coins:</b> ${gameVars['coins']}`;
  }

  // Actual update
  $('#currency-list').append(statsText);

};

function addContinueButton(label){
  const tempId = "continue-button-" + label;
  var button =  `<input type="button" id="${tempId}" class="continue-button"/>`

  if (label == 'lumber-keep' && $("#business-tab-label").is(":hidden")){
    console.log('Generating continue button for ' + label);

    $('#time-log').append(button);

    $('#' + tempId).on("click", function(){

      $('#business-tab-label').show();

      $('#' + tempId).remove();
      gameVars['unlocked'].push('#business-tab-label');
      var lumberHut = new Business("Jim's Lumber Hut", 4750, [itemList.log], [itemList.log], [upgradeList.output1], 'outdoor', 'lumber', 4);
      gameVars['ownedBusinesses'].push(lumberHut);
      // gameVars['ownedBusinesses'].push(new Business("aaa", 5000, [], [], [], 'underground', 'mushroom_underground', 3));
      updateBusinesses();
    });
  }
}

function updateStoryLog(titleKey){
  if (gameVars['logContents'] == 'null'){
    gameVars['logContents'] = ['intro', 'lumber-keep'];
  } else if (typeof gameVars['logContents'] == 'string') {
    gameVars['logContents'] = gameVars['logContents'].split(',');
  }

  for (var i = 0; i < arguments.length; i++){
    // Add item to array if not already present.
    if (!gameVars['logContents'].includes(argument[i])){
      gameVars['logContents'].push(arguments[i])
    }
  }


  $("#time-log").empty()
  for (const label in gameVars['logContents']){
    $("#time-log").append('<p">'+log[gameVars['logContents'][label]]+'</p>');

    if (log["continue-button"].includes(gameVars['logContents'][label])) {
      addContinueButton(gameVars['logContents'][label]);
    }

    $("#time-log").append('<hr>');
  }
};

function upgradePanel(business){
  console.log("upgrade panel for " + business);
}

function updateBusinesses(){
  updateLocalVariables();
  pullLocalVariables();

  $('#business-tab-content').empty();
  for (let business of gameVars['ownedBusinesses']){
    if (!(business instanceof Business)){
      console.error("Owned Businesses Borked.");
    }
    $('#business-tab-content').append(business.card);

    business.formatPaydays();
    business.crystalize();
    business.activateButtons();
  }

  makeItemsDraggable();
}

function transferItem(sourceInvItem, targetInvSlot){
  // closest $ w/ class of "inventory-slot"
  var cards = [
    sourceInvItem.closest('.inventory-slot'),
    targetInvSlot.closest('.inventory-slot')
  ];

  // Currently either 'inventory' or 'business'
  var types = [
    cards[0].data('containing-inventory').split(/-(.+)/)[0],
    cards[1].data('containing-inventory').split(/-(.+)/)[0]
  ];

  // your-inventory
  // jim-s-lumber-hut-output
  var names = [
    cards[0].data('containing-inventory').substring(types[0].length+1, cards[0].data('containing-inventory').length),
    cards[1].data('containing-inventory').substring(types[1].length+1, cards[1].data('containing-inventory').length)
  ];


  var finalValues = {
    sourceObj: null,
    sourceKey: null,
    targetObj: null,
    targetKey: null
  }

  const refList = ['source', 'target'];
  for (var i=0; i<2; i++){
    if (types[i] == 'inventory'){
      finalValues[refList[i]+'Obj'] = gameVars['inventories'][names[i]];
      finalValues[refList[i]+'Key'] = cards[i].data('inventory-slot-number');
    }else if(types[i] == 'business'){
      var businessName = 'business-' + names[i].substring(0, names[i].lastIndexOf('-'));
      var businessIndex = gameVars.ownedBusinesses.findIndex(x => x.id === businessName);
      var businessSlot = names[i].substring(names[i].lastIndexOf('-') + 1, names[i].length)+'Inventory';
      finalValues[refList[i]+'Obj'] = gameVars.ownedBusinesses[businessIndex][businessSlot];

      // arguments[i] gives source, then target dependant on transferItem(sourceInvItem, targetInvSlot) declaration
      if (arguments[i].data('inventory-slot-number') === undefined){
        finalValues[refList[i]+'Key'] = arguments[i].parent().data('inventory-slot-number');
      } else { // empty slot
        finalValues[refList[i]+'Key'] = arguments[i].data('inventory-slot-number');
      }

    }else{
      console.error(`inventory type not inventory or business: ${types[i]}`);
    }
  }

  console.log("DELME finalValues");
  console.log(finalValues);

  var temp = finalValues['sourceObj']['items'][finalValues['sourceKey']];
  finalValues['sourceObj']['items'][finalValues['sourceKey']] = finalValues['targetObj']['items'][finalValues['targetKey']]
  finalValues['targetObj']['items'][finalValues['targetKey']] = temp;
}

function updateInventories(){
  if ($.isEmptyObject(gameVars['inventories']) || gameVars['inventories'] == null){
    gameVars.inventories = {
      'your-items': new Inventory('inventory-Your Items', 10)
    }
    gameVars.inventories['your-items'].add(itemList['log']);
  }

  // clear sidebar inventory, then repopulate.
  $('#inventories').empty();
  for (let [name, inv] of Object.entries(gameVars['inventories'])){
    if (inv.active){
      if (!(inv instanceof Inventory)){
        Object.setPrototypeOf(gameVars['inventories'][name], Inventory.prototype);
      }
      $('#inventories').append(inv.card);
    }
  }

  makeItemsDraggable();
  makeSlotsDroppable();
}

function tempPulls(){
  var deltas = JSON.parse(localStorage.getItem('deltas'));
  if (Object.keys(deltas).length != 0){
    for (const delta in deltas){
      gameVars[delta] += deltas[delta];
    }
  }
  localStorage.setItem('deltas', JSON.stringify({}));
}

$(document).ready(function(){
  // localStorage.setItem('deltas', JSON.stringify({}));
  pullLocalVariables();

  // Cheat
  $("#soft-reset-button").on('click', function(){
    gameVars["logContents"] = ['intro', 'lumber-keep'];
    gameVars["unlocked"] = [];
    gameVars["ownedBusinesses"] = new Array;
    gameVars["globalEvents"] = [];
    gameVars["coins"] = 100;
    location.reload();
  })
  $("#add-day-to-businesses").on('click', function(){
    for (var business of gameVars["ownedBusinesses"]){
      business.addProgress(86400);
    }
  })
  $("#print-hut").on('click', function(){
    console.log(gameVars['ownedBusinesses'][0]);
  })
  $("#week-output-positive").on('click', function(){
    for (var business of gameVars["ownedBusinesses"]){
      business.days = [];
      business.progress = 0;
      business.progressPlayer = 0;
      for (var i = 0; i < 7; i++){
        business.addProgress(86400);
        business.profit += 50;
      }
    }
  })
  $("#clear-inventory-button").on('click', function(){
    for (var inv in gameVars.inventories){
      Object.setPrototypeOf(gameVars.inventories[inv], Inventory.prototype);
      gameVars.inventories[inv].clear();
      updateInventories();
      console.log('inventories cleared');
    }
  })
  $("#droppable-slots-button").on('click', makeSlotsDroppable());
  // endcheat

  // make items draggable when business tab clicked
  $('a[data-toggle="tab"]').on('shown.bs.tab', function (e) {
    if ($(e.target).attr("href") == '#business-tab-content'){
      makeItemsDraggable();
      makeSlotsDroppable();
    }
  });

  for (const unlockedTag in gameVars['unlocked']){
    console.log(gameVars['unlocked'][unlockedTag] + " already unlocked, showing");
    $(gameVars['unlocked'][unlockedTag]).show();
  }

  updateStoryLog();
  updateInventories();
  updateBusinesses();

  // Repeated events
  setInterval(updateStatsDisplay, 1000 * 1);
  setInterval(function(){incrementTime(timeDelta)}, 1000 * 1);
  setInterval(updateLocalVariables, 1000 * 60 * 1);
  setInterval(function(){
    for (var business of gameVars["ownedBusinesses"]){
      business.addProgress();
      business.updateProgressBar();
    }
  }, 1000) // each business gains progress each second.
  setInterval(tempPulls, 1000 * 1);

});

// Update local vars on close.
$(window).on('unload', function(){
  updateLocalVariables();
});

export {transferItem, getGameVars, updateBusinesses};
